//懒加载就是利用之前代码分割的时候，将代码分割的语法放到 import 一个异步回调函数中
// 当满足异步触发条件再去加载，就叫做懒加载

//预加载：

document.getElementById('btn').onclick = function(){
  //懒加载
  import(/* webpackChunkName:'test', webpackPrefetch: true  */'./test')
  .then(({mul})=>{
    //eslint-diable-next-line
    console.log(mul(4,5))
  })
  .catch(()=>{
    //eslint-diable-next-line
    console.log('文件加载失败~')
  })
}